sContent":["// Based on [Eric Meyer's reset 2.0](http://meyerweb.com/eric/tools/css/reset/index.html)\n// Global reset rules.\n// For more specific resets, use the reset mixins provided below\n@mixin global-reset {\n  html, body, div, span, applet, object, iframe,\n  h1, h2, h3, h4, h5, h6, p, blockquote, pre,\n  a, abbr, acronym, address, big, cite, code,\n  del, dfn, em, img, ins, kbd, q, s, samp,\n  small, strike, strong, sub, sup, tt, var,\n  b, u, i, center,\n  dl, dt, dd, ol, ul, li,\n  fieldset, form, label, legend,\n  table, caption, tbody, tfoot, thead, tr, th, td,\n  article, aside, canvas, details, embed, \n  figure, figcaption, footer, header, hgroup, \n  menu, nav, output, ruby, section, summary,\n  time, mark, audio, video {\n    @include reset-box-model;\n    @include reset-font; }\n  // Unlike Eric's original reset, we reset the html element to be compatible\n  // with the vertical rhythm mixins.\n  html {\n    @include reset-body; }\n  ol, ul {\n    @include reset-list-style; }\n  table {\n    @include reset-table; }\n  caption, th, td {\n    @include reset-table-cell; }\n  q, blockquote {\n    @include reset-quotation; }\n  a img {\n    @include reset-image-anchor-border; }\n  @include reset-html5; }\n\n// Reset all elements within some selector scope. To reset the selector itself,\n// mixin the appropriate reset mixin for that element type as well. This could be\n// useful if you want to style a part of your page in a dramatically different way.\n@mixin nested-reset {\n  div, span, applet, object, iframe,\n  h1, h2, h3, h4, h5, h6, p, blockquote, pre,\n  a, abbr, acronym, address, big, cite, code,\n  del, dfn, em, img, ins, kbd, q, s, samp,\n  small, strike, strong, sub, sup, tt, var,\n  b, u, i, center,\n  dl, dt, dd, ol, ul, li,\n  fieldset, form, label, legend,\n  table, caption, tbody, tfoot, thead, tr, th, td,\n  article, aside, canvas, details, embed, \n  figure, figcaption, footer, header, hgroup, \n  menu, nav, output, ruby, section, summary,\n  time, mark, audio, video {\n    @include reset-box-model;\n    @include reset-font; }\n  table {\n    @include reset-table; }\n  caption, th, td {\n    @include reset-table-cell; }\n  q, blockquote {\n    @include reset-quotation; }\n  a img {\n    @include reset-image-anchor-border; } }\n\n// Reset the box model measurements.\n@mixin reset-box-model {\n  margin: 0;\n  padding: 0;\n  border: 0; }\n\n// Reset the font and vertical alignment.\n@mixin reset-font {\n  font: inherit;\n  font-size: 100%;\n  vertical-align: baseline; }\n\n// Resets the outline when focus.\n// For accessibility you need to apply some styling in its place.\n@mixin reset-focus {\n  outline: 0; }\n\n// Reset a body element.\n@mixin reset-body {\n  line-height: 1; }\n\n// Reset the list style of an element.\n@mixin reset-list-style {\n  list-style: none; }\n\n// Reset a table\n@mixin reset-table {\n  border-collapse: collapse;\n  border-spacing: 0; }\n\n// Reset a table cell (`th`, `td`)\n@mixin reset-table-cell {\n  text-align: left;\n  font-weight: normal;\n  vertical-align: middle; }\n\n// Reset a quotation (`q`, `blockquote`)\n@mixin reset-quotation {\n  quotes: none;\n  &:before, &:after {\n    content: \"\"; \n    content: none; } }\n\n// Resets the border.\n@mixin reset-image-anchor-border {\n  border: none; }\n\n// Unrecognized elements are displayed inline.\n// This reset provides a basic reset for block html5 elements\n// so they are rendered correctly in browsers that don't recognize them\n// and reset in browsers that have default styles for them.\n@mixin reset-html5 {\n  #{elements-of-type(html5-block)} {\n    display: block; } }\n\n// Resets the display of inline and block elements to their default display\n// according to their tag type. Elements that have a default display that varies across\n// versions of html or browser are not handled here, but this covers the 90% use case.\n// Usage Example:\n//\n//     // Turn off the display for both of these classes\n//     .unregistered-only, .registered-only\n//       display: none\n//     // Now turn only one of them back on depending on some other context.\n//     body.registered\n//       +reset-display(\".registered-only\")\n//     body.unregistered\n//       +reset-display(\".unregistered-only\")\n@mixin reset-display($selector: \"\", $important: false) {\n  #{append-selector(elements-of-type(\"inline\"), $selector)} {\n    @if $important {\n      display: inline !important; }\n    @else {\n      display: inline; } }\n  #{append-selector(elements-of-type(\"block\"), $selector)} {\n    @if $important {\n      display: block !important; }\n    @else {\n      display: block; } } }\n","// MIX-INS\n@mixin font-smoothing($value: antialiased) {\n  @if $value == antialiased {\n    -moz-osx-font-smoothing: grayscale;\n    -webkit-font-smoothing: antialiased;\n  } @else {\n    -moz-osx-font-smoothing: auto;\n    -webkit-font-smoothing: subpixel-antialiased;\n  }\n}\n\n@viewport {\n  width: device-width;\n  zoom: 1;\n  min-zoom: 1;\n  max-zoom: 3;\n  user-zoom: fixed;\n}\n\n* {\n  // @include selection($beam-gray, $white)\n}\n\nhtml {\n  background: $blind-gray;\n  font-size: $em-normalize-ratio;\n}\n\nbody {\n  @include font-smoothing;\n  background-color: $default-background-color;\n  color: $default-text-color;\n  font-family: $default-font-family;\n  font-size: $default-font-size;\n  line-height: $default-line-height;\n\n  a {\n    text-decoration: none;\n  }\n}\n\nh1 {\n  font-size: $h1-font-size;\n  line-height: $h1-line-height;\n  margin: $h1-font-size 0;\n\n  a {\n    color: $default-text-color;\n\n    &:hover {\n      color: $link-color;\n    }\n  }\n}\n\nh2 {\n  font-size: $h2-font-size;\n  line-height: $h2-line-height;\n  margin: $h2-font-size + .8rem 0;\n  word-wrap: break-word;\n}\n\nh3 {\n  font-size: $h3-font-size;\n  font-weight: bold;\n  line-height: $h3-line-height;\n  margin: $h3-font-size + 1.5rem 0;\n  word-wrap: break-word;\n}\n\np {\n  margin: 0 0 $default-font-size + 1.0rem 0;\n  word-wrap: break-word;\n}\n\nhr {\n  background: $border-gray;\n  border: 0;\n  height: 1px;\n  margin: 24px 0;\n}\n\nul {\n  list-style: disc;\n  margin-bottom: $default-font-size + 1rem;\n  margin-left: 2rem;\n}\n\nol {\n  list-style-type: decimal;\n  margin-bottom: $default-font-size + 1rem;\n  margin-left: 2rem;\n}\n\na {\n  color: $link-color;\n}\n\npre {\n  background: $code-gray;\n  border: 1px solid $whity-light-gray;\n  font-family: $monospace;\n  font-size: 1.3rem;\n  line-height: 1.5;\n  margin: 3rem auto;\n  overflow: scroll;\n  padding: 1.4rem;\n\n  @include breakpoint($large-screen) {\n    font-size: 1.4rem;\n  }\n}\n\ncode {\n  font-family: $monospace;\n}\n\nstrong {\n  font-weight: bold;\n}\n\nem {\n  font-style: italic;\n}\n\nblockquote {\n  border-left: 3px solid $whity-light-gray;\n  color: lighten($default-text-color, 20);\n  margin: 3rem 1rem;\n  padding-left: 1rem;\n}\n\ntable {\n  margin-bottom: $default-font-size + 1rem;\n  margin-top: $default-font-size + 1rem;\n\n  tr {\n    background-color: $white;\n    border-top: 1px solid $whity-gray;\n    margin: 0;\n    padding: 0;\n\n    &:nth-child(2n) {\n      background-color: $code-gray;\n    }\n\n    th {\n      border: 1px solid $whity-gray;\n      font-weight: bold;\n      margin: 0;\n      padding: 6px 13px;\n      text-align: left;\n\n      &:first-child {\n        margin-top: 0;\n      }\n\n      &:last-child {\n        margin-bottom: 0;\n      }\n    }\n\n    td {\n      border: 1px solid $whity-gray;\n      margin: 0;\n      padding: 6px 13px;\n      text-align: left;\n    }\n\n    &:first-child {\n      margin-top: 0;\n    }\n\n    &:last-child {\n      margin-bottom: 0;\n    }\n  }\n}\n","// Custom begin\n$article-content-margin-top: 40px;\n// Custom end\n\n$blind-gray: #272727;\n$hardcore-gray: #3b3b3b;\n$vim-gray: #4a4a4a;\n$gentle-gray: #666;\n$beam-gray: #969696;\n$feather-gray: #b4b3b3;\n$whity-gray: #ccc;\n$whity-light-gray: #ddd;\n$blur-white: #f3f3f3;\n$white: #fff;\n$code-gray: #f7f7f7;\n$crammy-green: #28a1a1;\n$border-gray: rgba(228, 229, 233, .96);\n\n$monospace: Consolas, 'Liberation Mono', Menlo, Courier, monospace;\n$serif: 'Lato', sans-serif;\n\n// Typography\n$em-normalize-ratio: 62.5%;\n$h1-font-size: 2.8rem;\n$h1-line-height: 1.5;\n$h2-font-size: 2.6rem;\n$h2-line-height: 1.5;\n$h3-font-size: 1.6rem;\n$h3-line-height: 1.5;\n\n$content-width: 960px;\n$content-body-padding: 15px;\n$content-body-width: $content-width - ($content-body-padding * 2);\n$default-font-family: $serif;\n$default-text-color: $hardcore-gray;\n$default-background-color: $white;\n$top-menu-height: 40px;\n$top-menu-text-color: $feather-gray;\n$footer-text-color: $feather-gray;\n$link-color: $crammy-green;\n$default-font-size: 1.6rem;\n$default-line-height: 1.8;\n$mobile-font-size: 1.4rem;\n\n$large-screen: $content-width;\n$mobile-screen: 550px;\n","//////////////////////////////\n// Default Variables\n//////////////////////////////\n$Breakpoint-Settings: (\n  'default media': all,\n  'default feature': min-width,\n  'default pair': width,\n\n  'force all media type': false,\n  'to ems': false,\n  'transform resolutions': true,\n\n  'no queries': false,\n  'no query fallbacks': false,\n\n  'base font size': 16px,\n\n  'legacy syntax': false\n);\n\n$breakpoint: () !default;\n\n//////////////////////////////\n// Imports\n//////////////////////////////\n@import \"breakpoint/settings\";\n@import 'breakpoint/context';\n@import 'breakpoint/helpers';\n@import 'breakpoint/parsers';\n@import 'breakpoint/no-query';\n\n@import 'breakpoint/respond-to';\n\n@import \"breakpoint/legacy-settings\";\n\n//////////////////////////////\n// Breakpoint Mixin\n//////////////////////////////\n\n@mixin breakpoint($query, $no-query: false) {\n  @include legacy-settings-warning;\n\n  // Reset contexts\n  @include private-breakpoint-reset-contexts();\n\n  $breakpoint: breakpoint($query, false);\n\n  $query-string: map-get($breakpoint, 'query');\n  $query-fallback: map-get($breakpoint, 'fallback');\n\n  $private-breakpoint-context-holder: map-get($breakpoint, 'context holder') !global;\n  $private-breakpoint-query-count: map-get($breakpoint, 'query count') !global;\n\n  // Allow for an as-needed override or usage of no query fallback.\n  @if $no-query != false {\n    $query-fallback: $no-query;\n  }\n\n  @if $query-fallback != false {\n    $context-setter: private-breakpoint-set-context('no-query', $query-fallback);\n  }\n\n  // Print Out Query String\n  @if not breakpoint-get('no queries') {\n    @media #{$query-string} {\n      @content;\n    }\n  }\n\n  @if breakpoint-get('no query fallbacks') != false or breakpoint-get('no queries') == true {\n\n    $type: type-of(breakpoint-get('no query fallbacks'));\n    $print: false;\n\n    @if ($type == 'bool') {\n      $print: true;\n    }\n    @else if ($type == 'string') {\n      @if $query-fallback == breakpoint-get('no query fallbacks') {\n        $print: true;\n      }\n    }\n    @else if ($type == 'list') {\n      @each $wrapper in breakpoint-get('no query fallbacks') {\n        @if $query-fallback == $wrapper {\n          $print: true;\n        }\n      }\n    }\n\n    // Write Fallback\n    @if ($query-fallback != false) and ($print == true) {\n      $type-fallback: type-of($query-fallback);\n\n      @if ($type-fallback != 'bool') {\n        #{$query-fallback} & {\n          @content;\n        }\n      }\n      @else {\n        @content;\n      }\n    }\n  }\n\n  @include private-breakpoint-reset-contexts();\n}\n\n\n@mixin mq($query, $no-query: false) {\n  @include breakpoint($query, $no-query) {\n    @content;\n  }\n}\n","main {\n  margin: auto;\n  width: 100%;\n\n  @include breakpoint($content-body-width) {\n    overflow: hidden;\n    width: $content-width;\n  }\n\n  article {\n    margin-bottom: 100px;\n    padding: 0 $content-body-padding;\n\n    .article-title {\n      margin-top: 10rem;\n      text-align: center;\n    }\n\n    .tag-list {\n      list-style-type: none;\n    }\n\n    .tag-labels {\n      margin-left: .7rem; // centering\n      text-align: center;\n    }\n\n    .tag-label {\n      @include border-radius(13px);\n      background: darken($feather-gray, 4);\n      border: 0 none;\n      color: $white;\n      cursor: pointer;\n      display: inline-block;\n      font-size: 1.3rem;\n      font-weight: bold;\n      line-height: 1.3;\n      margin-bottom: .8rem;\n      margin-right: .7rem;\n      padding: .4rem 1.2rem .5rem;\n      text-align: center;\n\n      &:hover {\n        background: $link-color;\n      }\n\n      a {\n        color: $white;\n        width: 100%;\n      }\n    }\n\n    .posted-date-wrapper {\n      color: $gentle-gray;\n      margin-bottom: 25px;\n      margin-top: 40px;\n      text-align: center;\n\n      .posted-date {\n        padding-left: .4em;\n      }\n    }\n\n    .archive-list {\n      list-style-type: none;\n\n      .archive-posted-date-wrapper {\n        color: $gentle-gray;\n        margin-right: .8em;\n        .posted-date {\n          padding-left: .4em;\n        }\n      }\n    }\n\n    .article-header-separator {\n      margin-bottom: 50px;\n      margin-top: 50px;\n    }\n\n    img {\n      display: block;\n      margin: 40px auto;\n      max-width: 90%;\n    }\n\n    .read-more {\n      @include clearfix;\n      font-size: 1.5rem;\n      margin-top: 2rem;\n      padding-right: $content-body-padding;\n\n      a {\n        float: right;\n      }\n    }\n\n    .movie-wrapper {\n      margin: 40px auto;\n      padding: 0 $content-body-padding;\n      width: 90%;\n\n      @include breakpoint($content-body-width) {\n        width: 640px;\n      }\n\n      .movie {\n        position: relative;\n        width: 100%;\n\n        iframe {\n          height: 100%;\n          position: absolute;\n          right: 0;\n          top: 0;\n          width: 100%;\n        }\n      }\n    }\n\n    .paginate-wrapper {\n      @include pie-clearfix;\n      font-size: 1.5rem;\n      margin: 4rem;\n\n      .previous-page {\n        float: left;\n      }\n\n      .next-page {\n        float: right;\n      }\n    }\n\n    .page-number {\n      color: $gentle-gray;\n      font-size: 1.5rem;\n      margin-top: 3rem;\n      text-align: center;\n    }\n  }\n}\n","@import \"../support\";\n\n// This mixin provides basic support for CSS3 properties and\n// their corresponding experimental CSS2 properties when\n// the implementations are identical except for the property\n// prefix.\n@mixin experimental($property, $value,\n  $moz      : $experimental-support-for-mozilla,\n  $webkit   : $experimental-support-for-webkit,\n  $o        : $experimental-support-for-opera,\n  $ms       : $experimental-support-for-microsoft,\n  $khtml    : $experimental-support-for-khtml,\n  $official : true\n) {\n  @if $webkit  and $experimental-support-for-webkit    { -webkit-#{$property} : $value; }\n  @if $khtml   and $experimental-support-for-khtml     {  -khtml-#{$property} : $value; }\n  @if $moz     and $experimental-support-for-mozilla   {    -moz-#{$property} : $value; }\n  @if $ms      and $experimental-support-for-microsoft {     -ms-#{$property} : $value; }\n  @if $o       and $experimental-support-for-opera     {      -o-#{$property} : $value; }\n  @if $official                                        {         #{$property} : $value; }\n}\n\n// Same as experimental(), but for cases when the property is the same and the value is vendorized\n@mixin experimental-value($property, $value,\n  $moz      : $experimental-support-for-mozilla,\n  $webkit   : $experimental-support-for-webkit,\n  $o        : $experimental-support-for-opera,\n  $ms       : $experimental-support-for-microsoft,\n  $khtml    : $experimental-support-for-khtml,\n  $official : true\n) {\n  @if $webkit  and $experimental-support-for-webkit    { #{$property} : -webkit-#{$value}; }\n  @if $khtml   and $experimental-support-for-khtml     { #{$property} :  -khtml-#{$value}; }\n  @if $moz     and $experimental-support-for-mozilla   { #{$property} :    -moz-#{$value}; }\n  @if $ms      and $experimental-support-for-microsoft { #{$property} :     -ms-#{$value}; }\n  @if $o       and $experimental-support-for-opera     { #{$property} :      -o-#{$value}; }\n  @if $official                                        { #{$property} :         #{$value}; }\n}\n","// @doc off\n// Extends the bottom of the element to enclose any floats it contains.\n// @doc on\n\n@import \"hacks\";\n\n// This basic method is preferred for the usual case, when positioned\n// content will not show outside the bounds of the container.\n//\n// Recommendations include using this in conjunction with a width.\n// Credit: [quirksmode.org](http://www.quirksmode.org/blog/archives/2005/03/clearing_floats.html)\n@mixin clearfix {\n  overflow: hidden;\n  @include has-layout;\n}\n\n// This older method from Position Is Everything called\n// [Easy Clearing](http://www.positioniseverything.net/easyclearing.html)\n// has the advantage of allowing positioned elements to hang\n// outside the bounds of the container at the expense of more tricky CSS.\n@mixin legacy-pie-clearfix {\n  &:after {\n    content    : \"\\0020\";\n    display    : block;\n    height     : 0;\n    clear      : both;\n    overflow   : hidden;\n    visibility : hidden;\n  }\n  @include has-layout;\n}\n\n// This is an updated version of the PIE clearfix method that reduces the amount of CSS output.\n// If you need to support Firefox before 3.5 you need to use `legacy-pie-clearfix` instead.\n//\n// Adapted from: [A new micro clearfix hack](http://nicolasgallagher.com/micro-clearfix-hack/)\n@mixin pie-clearfix {\n  &:after {\n    content: \"\";\n    display: table;\n    clear: both;\n  }\n  @include has-layout;\n}\n","@import \"../../support\";\n\n// The `zoom` approach generates less CSS but does not validate.\n// Set this to `block` to use the display-property to hack the\n// element to gain layout.\n$default-has-layout-approach: zoom !default;\n\n// This mixin causes an element matching the selector\n// to gain the \"hasLayout\" property in internet explorer.\n// More information on [hasLayout](http://reference.sitepoint.com/css/haslayout).\n@mixin has-layout($approach: $default-has-layout-approach) {\n  @if $legacy-support-for-ie {\n    @if $approach == zoom {\n      @include has-layout-zoom;\n    } @else if $approach == block {\n      @include has-layout-block;\n    } @else {\n      @warn \"Unknown has-layout approach: #{$approach}\";\n      @include has-layout-zoom;\n    }\n  }\n}\n\n@mixin has-layout-zoom {\n  @if $legacy-support-for-ie6 or $legacy-support-for-ie7 {\n    *zoom: 1;\n  }\n}\n\n@mixin has-layout-block {\n  @if $legacy-support-for-ie {\n    // This makes ie6 get layout\n    display: inline-block;\n    // and this puts it back to block\n    & { display: block; }\n  }\n}\n\n// A hack to supply IE6 (and below) with a different property value.\n// [Read more](http://www.cssportal.com/css-hacks/#in_css-important).\n@mixin bang-hack($property, $value, $ie6-value) {\n  @if $legacy-support-for-ie6 {\n    #{$property}: #{$value} !important;\n    #{$property}: #{$ie6-value};\n  }\n}\n","$header-height: 40px;\n\nheader {\n  /*background-image: url(\"../images/drops.png\");*/\n  background-position: -300px $header-height;\n  background-repeat: no-repeat;\n  height: $header-height;\n  margin: 0 auto;\n\n  @include breakpoint($large-screen) {\n    background-position: center 40px;\n    background-size: contain;\n  }\n\n  @include breakpoint($large-screen) {\n    /*height: 400px;*/\n  }\n\n  .top-menu {\n    @include pie-clearfix;\n    background-color: $blind-gray;\n    height: $top-menu-height;\n    position: fixed;\n    top: 0;\n    width: 100%;\n    z-index: 100;\n\n    .top-menu-wrapper {\n      color: $top-menu-text-color;\n      margin: 0 auto;\n      padding-top: .9rem;\n      width: 100%;\n\n      @include breakpoint($large-screen) {\n        width: $content-width;\n      }\n\n      a {\n        color: $top-menu-text-color;\n\n        &:hover {\n          color: $whity-light-gray;\n        }\n      }\n\n      .top-menu-list {\n        float: left;\n        margin-left: 15px;\n        width: 80%;\n\n        @include breakpoint($large-screen) {\n          margin-left: 30px;\n        }\n\n        ul {\n          @include horizontal-list;\n\n          li {\n            font-size: 1.2rem;\n            margin: 0;\n\n            &:after {\n              content: '\\2219';\n              margin-left: 10px;\n              margin-right: 3px;\n            }\n\n            &:last-child:after { content: ''; }\n          }\n        }\n      }\n\n      .feed {\n        float: right;\n        line-height: 2.2rem;\n        text-align: left;\n        width: 10%;\n\n        @include breakpoint($large-screen) {\n          margin-right: 60px;\n          text-align: right;\n        }\n\n        i {\n          font-size: 1.8rem;\n        }\n      }\n    }\n  }\n}\n.logo {\n  margin: 0 auto;\n  padding: 60px 20px 0;\n\n  @include breakpoint($large-screen) {\n    padding: 100px 20px 0;\n  }\n\n  $site-title-logo-image: false;\n\n  @if $site-title-logo-image {\n    img {\n      display: block;\n      margin: 0 auto;\n      max-width: 100%;\n    }\n  } @else {\n    .logo-text {\n      color: #1d1d1d;\n      display: block;\n      font-size: 1.6em;\n      margin: 0 auto;\n      text-align: center;\n\n      @include breakpoint($large-screen) {\n        font-size: 2.5em;\n      }\n    }\n  }\n}\n","// Turn off the bullet for an element of a list\n@mixin no-bullet {\n  list-style-image : none;\n  list-style-type  : none;\n  margin-left      : 0;\n}\n\n// turns off the bullets for an entire list\n@mixin no-bullets {\n  list-style: none;\n  li { @include no-bullet; }\n}\n\n// Make a list(ul/ol) have an image bullet.\n//\n// The mixin should be used like this for an icon that is 5x7:\n//\n//     ul.pretty\n//       +pretty-bullets(\"my-icon.png\", 5px, 7px)\n//\n// Additionally, if the image dimensions are not provided,\n// The image dimensions will be extracted from the image itself.\n//\n//     ul.pretty\n//       +pretty-bullets(\"my-icon.png\")\n//\n@mixin pretty-bullets($bullet-icon, $width: image-width($bullet-icon), $height: image-height($bullet-icon), $line-height: 18px, $padding: 14px) {\n  margin-left: 0;\n  li {\n    padding-left: $padding;\n    background: image-url($bullet-icon) no-repeat ($padding - $width) / 2 ($line-height - $height) / 2;\n    list-style-type: none;\n  }\n}\n","// Horizontal list layout module.\n//\n// Easy mode using simple descendant li selectors:\n//\n//   ul.nav\n//     +horizontal-list\n//\n// Advanced mode:\n// If you need to target the list items using a different selector then use\n// +horizontal-list-container on your ul/ol and +horizontal-list-item on your li.\n// This may help when working on layouts involving nested lists. For example:\n//\n//   ul.nav\n//     +horizontal-list-container\n//     > li\n//       +horizontal-list-item\n\n@import \"bullets\";\n@import \"../../utilities/general/clearfix\";\n@import \"../../utilities/general/reset\";\n@import \"../../utilities/general/float\";\n\n// Can be mixed into any selector that target a ul or ol that is meant\n// to have a horizontal layout. Used to implement +horizontal-list.\n@mixin horizontal-list-container {\n  @include reset-box-model;\n  @include clearfix; }\n\n// Can be mixed into any li selector that is meant to participate in a horizontal layout.\n// Used to implement +horizontal-list.\n//\n// :last-child is not fully supported\n// see http://www.quirksmode.org/css/contents.html#t29 for the support matrix\n//\n// IE8 ignores rules that are included on the same line as :last-child\n// see http://www.richardscarrott.co.uk/posts/view/ie8-last-child-bug for details\n//\n// Setting `$padding` to `false` disables the padding between list elements\n@mixin horizontal-list-item($padding: 4px, $direction: left) {\n  @include no-bullet;\n  white-space: nowrap;\n  @include float($direction);\n